}
.First_screen {
  background-size: cover;

  height: 100%;
  background-image: url("/images/first-fon.png");
  background-repeat: no-repeat;
  position: relative;
  .fon {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    .rimword{
      img{
      width: 100%;
    }}
    .block{
      height: 100%;
      width: 100%;
      .img{
        img{
          position: absolute;
          bottom: 0;
          left: 0;
          width: 800px;
          //width: 100%;
        }
      }

      .rimword{
        position: relative;
        text-align: left;
        padding-right: 50px;
        margin-bottom: 140px;
        p{
          max-width: 500px;
          margin-bottom: 26px;
          font-family: Manrope;
          text-align: left;
          font-style: normal;
          font-weight: 600;
          font-size: 34px;
          line-height: 40px;
          letter-spacing: 0.05em;
          color: #5F6AF1;
        }
        img{
          margin-bottom:99px ;
        }
        h1{
          width: 100%;
          font-family: Manrope;
          font-style: normal;
          font-weight: 800;
          font-size: 4em;
          line-height: 90px;
          /* or 100% */
          display: flex;
          align-items: center;
          letter-spacing: 0.05em;

          color: #FFFFFF;
        }
        button{
          background: #E7EBFF;
          width: 195.48px;
          height: 54px;
          border-radius: 50px;
        }
      }
    }
  }
}
